import React, {FormEvent, Profiler, SyntheticEvent, useEffect, useRef, useState} from 'react';
import './App.css';
import AddText from "./components/AddText";
import createStamp, {deleteStamp, LifeStamp, merge, toggleFavorite, upgradeStampsVersion} from "./lib/stamps.utils";
import useLocalList from "./components/hooks/localStorage.hook";
import InputFile from "./components/InputFile";
import DownloadFile from "./components/DownloadFile";
import {StampsContext} from './components/contexts/StampsContext.contexts';
import {StampsContainer} from "./components/Stamps/StampsContainer";
import {jstr} from "./lib/string.utils";
import {TagContainer} from "./components/Tags/TagDropdown";
import {onRenderCallback} from "./utils/onRender";

function App() {
    let renderCount = useRef(0);
    renderCount.current = renderCount.current + 1;
    console.log(renderCount.current);

    const {
        addAtBeginning: addStampToLocal,
        fetch: fetchStampsFromLocal,
        clear: clearStampsFromLocal
    } = useLocalList('LIFESTAMPS');

    const [newNoteValue, setNewNoteValue] = useState('');
    const [stamps, setStamps] = useState(fetchStampsFromLocal<LifeStamp>());
    const [search, setSearch] = useState('');
    const [filter, setFilter] = useState('');

    useEffect(() => {
        function onLoad() {
            const newStamps = upgradeStampsVersion(stamps);
            clearStampsFromLocal();
            newStamps.forEach((stamp: LifeStamp) => addStampToLocal<LifeStamp>(stamp));
            setStamps(newStamps);
        }

        onLoad();
    }, []);

    function onTextInput(e: FormEvent<HTMLTextAreaElement>) {
        setNewNoteValue(e.currentTarget.value);
    }

    function onAddStamp(e: SyntheticEvent<HTMLButtonElement>) {
        if (newNoteValue) {
            const stamp = createStamp(newNoteValue, stamps.length + 1);
            setStamps([stamp, ...stamps]);
            addStampToLocal<LifeStamp>(stamp);
            setNewNoteValue('');
        }
    }

    function onDeleteStamp(stamp: LifeStamp) {
        if (stamp) {
            console.log('deleting a stamp', stamp);
            const newStamps = deleteStamp(stamp.serial, stamp.id, stamps);
            clearStampsFromLocal();
            newStamps.forEach((stamp: LifeStamp) => addStampToLocal<LifeStamp>(stamp));
            setStamps(newStamps);
        }
    }

    function onToggleFavorite(stamp: LifeStamp) {
        if (stamp) {
            console.log('toggling favorite', jstr(stamp));
            const newStamps = toggleFavorite(stamp.serial, stamp.id, stamps);
            clearStampsFromLocal();
            newStamps.forEach((stamp: LifeStamp) => addStampToLocal<LifeStamp>(stamp));
            setStamps(newStamps);
        }
    }

    function onFileRead(data: LifeStamp[]) {
        const mergedLifeStamps = merge<LifeStamp>(stamps, data);
        clearStampsFromLocal();
        mergedLifeStamps.forEach((stamp: LifeStamp) => addStampToLocal<LifeStamp>(stamp));
        setStamps(mergedLifeStamps);
    }

    function onSearchInput(e: SyntheticEvent<HTMLInputElement>) {
        setSearch(e.currentTarget.value.toLocaleLowerCase());
    }

    return (
        <div id="App">
            <Profiler id={"app"} onRender={onRenderCallback}>
                <StampsContext.Provider value={{
                    stamps,
                    onAddStamp,
                    onTextInput,
                    textValue: newNoteValue,
                    onDeleteStamp,
                    onToggleFavorite,
                    search,
                    filter,
                    setFilter
                }}>
                    <header className="header">
                        <h1 className="brand"
                            onClick={() => window.location.href = "/lifestamps"}>LifeStamps</h1>

                    </header>
                    <section id="actions">
                        <div className="search-container">
                            <label htmlFor={"searchTag"}>Search by tag</label>
                            <input type={"text"}
                                   id="searchTag"
                                   name="searchTag" placeholder={"search"} onInput={onSearchInput} value={search}/>
                        </div>
                        <TagContainer/>

                        <AddText/>
                    </section>
                    <section className="content">
                        <StampsContainer/>
                    </section>
                    <footer id="footer">
                        <DownloadFile/>
                        <InputFile actions={{onFileRead}}/>
                        {/*<h6>{process.env.REACT_APP_NAME} by anubhav | {process.env.REACT_APP_VERSION}</h6>*/}
                    </footer>
                </StampsContext.Provider>
            </Profiler>
        </div>
    );
}

export default App;
