import {StampsContext} from "../contexts/StampsContext.contexts";
import React from "react";
import ListStamps from "./ListStamps";

export function StampsContainer() {
    return <StampsContext.Consumer>
        {
            ({stamps, onDeleteStamp, onToggleFavorite, search, filter, setFilter}) =>
                <ListStamps
                    key="lifestamps-container"
                    stamps={stamps}
                    onDeleteStamp={onDeleteStamp}
                    onToggleFavorite={onToggleFavorite}
                    searchTerm={search}
                    filter={filter}
                    setFilter={setFilter}
                />
        }
    </StampsContext.Consumer>;
}