import React, {SyntheticEvent} from "react";

import uploadIcon from '../icons/file_upload_black_24dp.svg';

export default function InputFile(props: any) {
    const {onFileRead} = props.actions;

    function onFile(e: SyntheticEvent<HTMLInputElement>) {
        const file = e?.currentTarget?.files?.[0];
        const reader = new FileReader();
        reader.onload = function (e) {
            const data = JSON.parse(e!.target!.result as string);
            onFileRead(data);
            console.log(data);
        };
        reader.readAsText(file!, 'utf-8');
    }

    return <div style={{display: "grid", gridTemplateColumns: "1fr 1fr"}}>
        <label htmlFor="upload" style={{cursor: 'pointer'}} className="upload-control">
            Upload your lifestamps
        </label>
        <input
            type="file"
            id="upload"
            name="upload"
            accept="application/json"
            onChange={onFile}
            placeholder="Upload your lifestamps"

        />
    </div>;
}
